.theme1{--bgColorMain: rgb(201, 202, 217);--fontColorMain: rgb(0,0,0);--fontFamily: "Arvo", Times, sans-serif;--borderStyle: 1px solid rgb(209, 210, 249);--linkColorMain: rgb(119, 150, 203);--linkColorHover: rgb(87, 100, 144)}.theme2{--bgColorMain: rgb(197, 213, 234);--fontColorMain: rgb(0,0,0);--fontFamily: "Arvo", Times, sans-serif;--borderStyle: 1px solid rgb(117, 158, 184);--linkColorMain: rgb(115, 146, 183);--linkColorHover: rgb(179, 197, 215)}a,a:visited{color:var(--linkColorMain)}a:hover{color:var(--linkColorHover)}h1,h2,h3,h4,h5,h6,p{padding:0px;margin:0px}.layout{width:100%;height:100%;background-color:var(--bgColorMain);color:var(--fontColorMain);font-family:var(--fontFamily);display:grid;grid-template-rows:90px auto;grid-template-areas:"header" "page_content"}.layout.collapsed{grid-template-rows:calc(100% - 20px)}.site-header{grid-area:header;background-color:var(--headerColor);border-bottom:var(--borderStyle)}.site-header.collapsed{display:none}.collapse-header{width:100%;height:20px;position:absolute;top:70px;display:grid;justify-content:center;align-content:center}.collapse-header.collapsed{top:0px;z-index:1}.collapse-icon{font-size:3.3em;cursor:pointer}.collapse-icon:hover{color:var(--fontColorHover)}.page-content-wrapper{width:100%;height:100%;overflow:hidden;position:relative;grid-area:"page_content"}.page-content-wrapper.collapsed{height:100%;margin-top:20px}.page-content{padding:10px;position:absolute;top:0;bottom:0;left:0;right:-17px;overflow-y:scroll}